import React from "react"
import { Link, graphql } from "gatsby"
import moment from "moment"
import kebabCase from "lodash/kebabCase"

import Bio from "../components/bio"
import Layout from "../components/layout"
import Seo from "../components/seo"

const Tags = ({ data, pageContext, location }) => {
  const siteTitle = data.site.siteMetadata?.title || `Title`
  const { totalCount } = data.allMarkdownRemark
  const posts = data.allMarkdownRemark.nodes
  const { tag } = pageContext

  if (posts.length === 0) {
    return (
      <Layout location={location} title={siteTitle}>
        <Seo title={`タグ: "${tag}" (0記事)`} />
        <p>該当するタグの投稿記事がありません。</p>
      </Layout>
    )
  }

  const tagHeader = `タグ検索`
  const tagCount = `"${tag}": ${totalCount}件見つかりました`
  return (
    <Layout location={location} title={siteTitle}>
		<header>
			<section>
				<h1>{tagHeader}</h1>
				<p className="p">{tagCount}</p>
			</section>
		</header>
		<main>
			<article>
				<ol style={{ listStyle: `none` }}>
					{posts.map(post => {
					const title = post.frontmatter.title || post.fields.slug
					const tags = post.frontmatter.tags

					return (
						<li key={post.fields.slug}>
						<article
							className="post-list-item"
							itemScope
							itemType="http://schema.org/Article"
						>
							<header>
							<h2 className="page-title">
								<Link to={post.fields.slug} itemProp="url">
								<span itemProp="headline">{title}</span>
								</Link>
							</h2>
							<p className="p">{moment(post.frontmatter.date).format(`YYYY年MM月DD日 HH:mm`)}</p>
								<div className="p tags-article">
									{tags && tags.length > 0 && tags.map(tag => {
										return (
											<Link to={`/tags/${kebabCase(tag)}/`} itemProp="url">
												<button className="tags-button">{tag}</button>
											</Link>
										)
									})}
								</div>
							</header>
							<section>
							<p
								className="p"
								dangerouslySetInnerHTML={{
								__html: post.frontmatter.description || post.excerpt,
								}}
								itemProp="description"
							/>
							</section>
						</article>
						</li>
					)
					})}
				</ol>
			</article>
			<Bio />
		</main>
    </Layout>
  )
}

export default Tags

export const pageQuery = graphql`
  query ($tag: String) {
    site {
      siteMetadata {
        title
      }
    }
    allMarkdownRemark(
      limit: 2000
      sort: { fields: [frontmatter___date], order: DESC }
      filter: { frontmatter: { tags: { in: [$tag] } } }
    ) {
      totalCount
      nodes {
        excerpt
        fields {
          slug
        }
        frontmatter {
          date
          title
          description
          tags
        }
      }
    }
  }
`